.lead {
  padding: var(--space-5) 0;
}

.leadGrid {
  display: grid;
  gap: var(--space-3);
  align-items: center;
}

.leadCopy p {
  color: var(--muted);
}

.leadForm {
  display: grid;
  gap: var(--space-2);
}

.formRow {
  display: grid;
  gap: var(--space-2);
}

.leadForm input,
.leadForm textarea {
  padding: 0.9rem 1rem;
  border-radius: 16px;
  border: 1px solid var(--accent);
  background: #fff;
  font: inherit;
}

.formNote {
  color: var(--muted);
  min-height: 1.2rem;
}

@media (min-width: 768px) {
  .leadGrid {
    grid-template-columns: 1.2fr 1fr;
  }

  .formRow {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
